import * as Categories from '../database/categories.json';
import * as Words from '../database/content.json';

class Games_History {
    wordsAndCategories: any = {
        categories: [],
        words: []
    };
    savedWords: any = {};

    addCategoriesToJsonFile () {
        let wordsAndCategories = this.getWordsAndCategories();
        if(wordsAndCategories) {
            wordsAndCategories.categories.map((item: any) => {
                if((Categories.RECORDS.length) < (20 + wordsAndCategories.categories.length)) {
                    let obj: any = {
                        ID: Categories.RECORDS.length + 3,
                        CategoryName: item,
                        Img: 'fa fa-user-plus'
                    }
                    Categories.RECORDS.push(obj);
                }
                return null;
            })
        }
    }
    
    saveCategory (name: any) {
        this.getWordsAndCategories();
        if(this.savedWords) {
            this.wordsAndCategories = this.savedWords;
        }
        this.wordsAndCategories.categories.push(name);
        localStorage.setItem('Words',JSON.stringify(this.wordsAndCategories));
        this.addCategoriesToJsonFile();
    }

    removeCategory (index: number) {
        this.getWordsAndCategories();
        this.savedWords.categories.splice(index,1);
        localStorage.setItem('Words',JSON.stringify(this.savedWords));
    }

    saveWord (state: any) {        
        this.getWordsAndCategories();
        if(this.savedWords) {
            this.wordsAndCategories = this.savedWords;
        }
        let categoryId = 0;
        Categories.RECORDS.map((item: any) => {
            if(state.categorySelect === item.CategoryName) {
                categoryId = item.ID;
            }
            return null;
        })
        let obj = {
            content: state.wordInput,
            category: categoryId,
            point: state.pointSelect
        }
        this.wordsAndCategories.words.push(obj);
        this.addWordsToJsonFile();
        localStorage.setItem('Words',JSON.stringify(this.wordsAndCategories));
    }

    removeWord (index: number) {
        this.getWordsAndCategories();
        this.savedWords.words.splice(index,1);
        localStorage.setItem('Words',JSON.stringify(this.savedWords));
    }

    addWordsToJsonFile () {
        let wordsAndCategories = this.getWordsAndCategories();
        if(wordsAndCategories) {
            wordsAndCategories.words.map((item: any) => {
                if((Words.RECORDS.length) < (18600 + wordsAndCategories.words.length)) {
                    let obj: any = {
                        ID: String(Words.RECORDS.length + 1),
                        content: item.content,
                        cat_id: String(item.category),
                        point: String(item.point)
                    }
                    Words.RECORDS.push(obj);
                }
                console.log(Words.RECORDS);
                
                return null;
            })
        }
    }

    getWordsAndCategories () {
        let result: any;
        result = localStorage.getItem('Words');
        this.savedWords = JSON.parse(result);
        return JSON.parse(result);
    }
}

export default Games_History;